<template>
	<div id=friendsCircle >
		<div class="top" @click="showPopup">
			<div class="fixed" :class='isBg?"bg":""' >
				<img src="~img/images/friendsCircle/left.png" alt="" class='left' v-show='!isBg' @click.stop='$router.go(-1)'>
				<img src="~img/images/friendsCircle/info.png" alt="" class='camera' v-show='!isBg' @click.stop='showInfo=true'>
				<img src="~img/images/friendsCircle/left2.png" alt="" class='left' v-show='isBg' @click.stop='$router.go(-1)'>
				<img src="~img/images/friendsCircle/info2.png"  alt="" class='camera' v-show='isBg' @click.stop='showInfo=true'>
				<span class='pyq' v-show='isBg'>我的相册</span>
			</div>
			<div class="myInfo">
				<p>{{myName}}</p>
				<img src="http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg" alt="" @click.stop='goMyInfo'>
			</div>	
			<span class='myDesc'>任何一颗成熟的心灵都必须经过寂寞的洗礼</span>
		</div>
		<div class="nowTime">
			<p>2019年</p>
			<p>今天
				<ul>
					<li>
						<span @click.stop='photo'></span>
					</li>
					<li>
						<span ></span>
						<p>从来不跌倒不算光彩，每次跌倒后能再站起来，才是最大的荣耀。</p>
					</li>
				</ul>
			</p>
		</div>
		<br>
		<div class="bottomLine">
			<i></i> <span></span> <i></i>
		</div> 
		</van-pull-refresh>
		<van-popup v-model="isShowPop" :duration=0.2>
			<ul>
				<li>更换相册封面</li>
			</ul>
		</van-popup>
		<van-popup v-model="isPhoto" :duration=0.2>
			<ul>
				<li>
					拍摄
					<span>照片或视频</span>
				</li>
				<li>
					从相机选择
				</li>
			</ul>
		</van-popup>
		<van-popup
			  v-model="showInfo"
			  position="right"
			  :style="{ height: '100%',width:'100%' }"
				:close-on-click-overlay='false'
				:overlay='false'
				:duration=0.2
			>
			<similarHeader title='消息' @goBack='showInfo=false' :haveMore='false'>
				<span class="clearInfo">清空</span>
			</similarHeader>
			<p class='noInfo'>暂无消息</p>
		</van-popup>
		<van-image-preview
		  v-model="showImg"
		  :images="images"
		  :show-index='false'
		  :show-indicators='true'
		  :start-position='nowImgindex'
		>
		</van-image-preview>
	</div>
</template>
<script>
	// import list from '@/assets/json/friendsCircle.json'
	import  similarHeader from '@/components/similar-header'
	export default{
		name:'friendsCircle',
		data(){
			return{
				myName:'',
				isBg:false,
				showInfo:false,
				isShowMore:false,
				friendsList:null,
				nowIndex:null,
				isShowPop:false,
				isPhoto:false,
				showImg:false,
				images:[],
				nowImgindex:0
			}
		},
		methods:{
		    goMyInfo(){
		    	let myName=sessionStorage.getItem('myName');
		    	sessionStorage.setItem('nowChatUser',myName)
				sessionStorage.setItem('nowUserAvator','http://img2.imgtn.bdimg.com/it/u=3363472252,3522967664&fm=26&gp=0.jpg')
				sessionStorage.setItem('nowChatNickname','')
				sessionStorage.setItem('nowChatNum','Maple_Story')
				sessionStorage.setItem('nowUserSex','男')
				sessionStorage.setItem('nowUserArea','中国 浙江')
				this.$router.push('/userInfo')
		    },
		    goTop(){
		    	document.documentElement.scrollTop=0;
		    	document.body.scrollTop=0;
		    	this.isLoading=true;
		    	this.nowIndex=null;
		    	this.onRefresh();
		    },
		    showPopup() {
		      this.isShowPop = true;
		    },
		    photo(){
		    	this.isPhoto=true;
		    },
		    showImgs(val,nowImgindex){
		    	this.images=val;
		    	this.showImg=true;
		    	this.nowImgindex=nowImgindex;
		    }
		},
		mounted(){
			// this.friendsList=list.data;
		},
		activated() {
			this.myName=sessionStorage.getItem('myName');
			let friendsTop=sessionStorage.getItem('friendsTop');
			if(friendsTop){
				document.documentElement.scrollTop=document.body.scrollTop=friendsTop;
			}	
	      	window.onscroll=()=>{
	      		this.nowIndex=null;
	      		var sTop=document.documentElement.scrollTop|| document.body.scrollTop;
	      		sessionStorage.setItem('friendsTop',sTop);
	      		var winScrollTop=document.documentElement.scrollTop|| document.body.scrollTop;
				if(winScrollTop>120){
					this.isBg=true;								
				}
				else{
					this.isBg=false;
				}
	      	} 
	    },
	    deactivated () {
		    window.onscroll=null;   
		},
		beforeRouteLeave (to, from, next) {
			this.nowIndex=null;
			if(this.isPhoto || this.isShowPop || this.showInfo){
				this.isPhoto=false;
				this.isShowPop=false;
				this.showInfo=false;
				let friendsTop=document.documentElement.scrollTop|| document.body.scrollTop;
				document.documentElement.scrollTop=document.body.scrollTop=friendsTop;
				this.$router.go(1);
		    }	
		    else if(this.showImg){
		    	this.showImg=false;
				next(false);
		    }
		    else{
		    	next();
		    }	
		},
		components:{
			similarHeader
		}
	}
</script>
<style scoped>
	#friendsCircle{
		background: #fff;
	}
	.top{
		height: 44vh;
		background:url(~img/images/chat/chatBg.jpg) no-repeat center/cover;
		position: relative;
		margin-bottom: 12.5rem;
	}
	.fixed{
		position: fixed;
	    width: 100%;
	    display: flex;
	    align-items: center;
	    padding: .5rem 0;
	    transition: .3s;
	    z-index: 6;
	    top:0;
	    left:0;
	}
	.bg{
		background: #ededed;
	}
	.fixed img{
		width: .48rem;
		height: .48rem;
	}
	.fixed .left{
		position: absolute;
   		left: .3rem;
   		width: .42rem;
		height: .42rem;
	}
	.camera{
	    position: absolute;
	    right: .3rem;
	}
	.pyq{
		font-size: .43rem;
	    position: absolute;
	    left: 1rem;
	}
	.myInfo{
		display: flex;
	    align-items: center;
	    position: absolute;
	    bottom: -.3rem;
	    right: .3rem;
	}
	.myInfo>p{
		font-size: .38rem;
	    font-weight: bold;
	    color: #fff;
	    margin-right: .3rem;
	}
	.myInfo>img{
		width: 1.27rem;
	    height: 1.27rem;
	    border-radius: .12rem;
	}
	
	.attached_image{
		display: flex;
		flex-wrap: wrap;
	}
	.attached_image img{
		max-width: 87%;
		height: 2.8rem;
		margin-bottom: .12rem;
	}
	.attached_image img.styleImg{
		width: 1.6rem;
	    height: 1.6rem;
	    margin: .06rem;
	}
	.van-popup--center{
		font-size: .39rem;
		padding: .32rem;
    	padding-left: .4rem;
    	width: 90vw;
		border-radius: .17rem;
		box-sizing: border-box;
	}
	.van-popup--center:not(:first-child){
		padding: 0 !important;
	}
	.van-popup--center ul li{
		font-size: .38rem;
		padding: .32rem;
	}
	.van-popup--center ul li:first-child{
		border-bottom: 1px solid #eee;
		position: relative;
	}
	.van-popup--center ul li:first-child span{
		font-size: .3rem;
	    position: absolute;
	    right: .4rem;
	    top: .36rem;
	    color:#888;
	}
	#friendsCircle >>> .van-image-preview__overlay  {
		background: rgba(0,0,0,1);
	}
	#friendsCircle >>> .van-swipe__indicators {
		bottom: .6rem;
	}
	#friendsCircle >>> .van-swipe__indicator:not(:last-child) {
	    margin-right: .18rem;
	}

	.myDesc{
		font-size: .34rem;
	    position: absolute;
	    right: .3rem;
	    width: 70vw;
	    bottom: -1.4rem;
	    text-align: right;
	    color:#737375;
	}
	.nowTime{
		font-size: .5rem;
		margin-left: .4rem;
		font-weight: bold;
	}
	.nowTime p:first-child{
		margin-bottom: .13rem;
		letter-spacing: .03rem;
	}
	.nowTime>p:last-child{
		display: flex;
	}
	.nowTime ul{
		display: inline-block;
		flex:1;
    	vertical-align: text-top;
	}
	.nowTime ul li{
		display: flex;
		align-items: flex-start;
		font-size: .32rem;
		margin:.1rem 0;
	}
	.nowTime ul li p{
		width: 65%;
   		margin-left: .2rem;
		font-weight: 400;
		overflow: hidden;      
		text-overflow: ellipsis;      
		display: -webkit-box;  
		-webkit-line-clamp: 2; 
		-webkit-box-orient: vertical;  

	}
	.nowTime ul li span{
		display: inline-block;
		width: 1.5rem;
		height: 1.5rem;
		background:#d8d8d6 url(~img/images/friendsCircle/myCamera.png) no-repeat center/60%;
		vertical-align: text-top;
		margin-left: .3rem;
	}
	.nowTime ul li:nth-of-type(2) span{
   		background:#d8d8d6 url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1567676454322&di=fbef3f571ccfec83a9cc0ad41f923d4d&imgtype=0&src=http%3A%2F%2Fimg02.tooopen.com%2Fimages%2F20160331%2Ftooopen_sy_157948668912.jpg) no-repeat center/100% 100%;
	}
	.clearInfo{
		position: absolute;
	    right: 0.3rem;
	    top: 50%;
	    transform: translateY(-50%);
	    font-weight: 350;
	    color:#ddd;
	    font-size: .4rem;
	}
	.noInfo{
		text-align: center;
	    font-size: .41rem;
	    margin-top: .4rem;
	}
	.bottomLine{
		text-align: center;
		height: 1px;
		width: 90vw;
		display:flex;
		justify-content: center;
		align-items: center;
		margin:0 auto;
		margin-bottom: .5rem;
	}
	.bottomLine i{
		display: inline-block;
		background: pink;
		height: 1px;
		width: 34%;
	}
	.bottomLine i:first-child{
		margin-right: .22rem;
	}
	.bottomLine i:last-child{
		margin-left: .22rem;
	}
	.bottomLine span{
		display: inline-block;
		width: 5px;
		border-radius: 50%;
		background: pink;
		height:5px;
		margin: 0 -.1rem;
	}
</style>


